body{
  background: linear-gradient(to bottom, #eebcae 0%, #e994e3 100%);
  background-repeat: no-repeat;
  height: 100%;
  background-attachment: fixed;
}

.wrapper {
  place-items: center;
  left: 50%;
  justify-content: center;
}

.wrapping {
  margin: auto;
  display: flex;
}

.grid {
padding-top: 2%;
display: grid;
grid-template-columns: repeat(3, 1fr);
gap: 10px; 
position: relative;
margin: auto;

}

button {

  background-color: rgba(67, 0, 121, 0.301);
  border: none;
  color: #ffccfc;
  box-shadow: 0px 8px 15px rgba(0, 0, 0.1, 0.2);
  border-radius: 60%;
  font-size: 35px;

  }

button:hover {
  background-color: rgba(44, 0, 80, 0.459);
  transform: rotate(360deg);
  transform: scale(1.03);
 }


span {

	text-align: center;
}

.message {
  background: #ededed3d;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
margin-top: 5%;
box-shadow: 0px 8px 15px rgba(0, 0, 0.1, 0.2);
height: 40px;
	width: 200px;
	font-size: 30px;
	border: 1px solid #d5d5d5;
  color: #570080;
}
 button:disabled {
  background: #6161613d;
  color: #b3b3b3;
  transform: none;
 }

 .texting {
   justify-content: center;
   text-align: center;
   color: #570080;
   font-size: large;
   display: block;
   position:relative;

 }

 /* //////////////responsive CSS here//////////// */

@media only screen and (min-width: 768px) {
  section.dashboard .slick-list .slick-track {
    display: flex;
  }
  section.dashboard .slick-list .slide {
    opacity: 1;
  }
  header .wrapper .article h1 span.arrow {
    display:none;
  }

  header .wrapper .article .description {
    max-height: 300px
  }
  

} 


@media only screen and (min-width: 1024px) {


  .container header .header-nav-area #nav_container  {
    display:flex;
  }

  .container header form {
    display:block;
  }

  .container header .menu-icon {
    display:none;
  }

  header .wrapper .article footer {
    display: block;
  }

  section.dashboard .slick-list .slick-track {
    display: flex;
    min-width: 309px;
    padding: 20px;
  }
  
  section.dashboard .slick-list .slick-track[index="2"] {
    display: flex;
  }

  section.dashboard .slick-list .slide {
    opacity: 1;
  }
} 